.searchBox {
    display: flex;
	align-items: center;
	justify-content: center;
	margin-bottom: 0.2rem;
	position: absolute;
	top: 170px;
    background: #2f3640;
    height: 40px;
    border-radius: 40px;
    padding: 10px;

}

.searchBox:hover > .searchInput {
    width: 240px;
    padding: 0 6px;
}

.searchBox:hover > .searchButton {
  background: white;
  color : #2f3640;
}

.searchButton {
    color: white;
    float: right;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: #2f3640;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 0.4s;
}

.searchInput {
    border:none;
    background: none;
    outline:none;
    float:left;
    padding: 0;
    color: white;
    font-size: 16px;
    transition: 0.4s;
    line-height: 40px;
    width: 0px;

}

@media screen and (max-width: 620px) {
.searchBox:hover > .searchInput {
    width: 150px;
    padding: 0 6px;
}
}

/* >>> TABLET <<< */
@media only screen and (min-width: 768px) {
	.searchBox {
		position: unset;
		top: 0;
	}
}

/* >>> DESKTOP <<< */
@media only screen and (min-width: 1280px) {  
	.searchBox {
		position: unset;
		top: 0;
	}
}